import { useState } from "react"
import { createWorker } from "tesseract.js"

// import { Tesseract } from "tesseract.ts";

// OCR
const STATUSES = {
    IDLE: "",
    FAILED: "OCR",
    PENDING: "OCR...",
    SUCCEEDED: "OCR",
}

function OcrReader({ onReadOcrData, onRemoveClicked }) {
    const [selectedImage, setSelectedImage] = useState(null)
    const [ocrState, setOcrState] = useState(STATUSES.IDLE)
    const [ocrText, setOcrText] = useState("")
    // const worker = createWorker("jpn")

    // OCR
    const readImageText = async () => {
        setOcrState(STATUSES.PENDING)

        try {
            (async () => {
                const worker = await createWorker("jpn");
                // await worker.setParameters({
                //     chop_enable: true,
                //     use_new_state_cost: false,
                //     segment_segcost_rating: false,
                //     enable_new_segsearch: 0,
                //     language_model_ngram_on: 0,
                //     textord_force_make_prop_words: false
                // });
                const { data: { text } } = await worker.recognize(selectedImage);
                console.log(text.replaceAll(" ", ""));
                await worker.terminate();
                setOcrText(text.replaceAll(" ", ""))
            })();
            setOcrState(STATUSES.SUCCEEDED)

        } catch (err) {
            console.log(err)
            setOcrState(STATUSES.FAILED)
        }

        // try {
        //     await worker.load()
        //     // OCR
        //     await worker.loadLanguage("jpn")
        //     await worker.initialize("jpn")
        //     const { data: { text } } = await worker.recognize(selectedImage)
        //     await worker.terminate()

        //     // 
        //     const strippedText = text.replace(/\s+/g, "")
        //     onReadOcrData(strippedText)
        //     setOcrState(STATUSES.SUCCEEDED)
        // } catch (err) {
        //     console.log(err)
        //     setOcrState(STATUSES.FAILED)
        // }
        // Tesseract
        //     .recognize(selectedImage, {
        //         lang: "jpn"
        //     })
        //     .progress(console.log)
        //     .then((res: any) => {
        //         console.log(res.text);
        //         setOcrState(STATUSES.SUCCEEDED)
        //     })
        //     .catch(console.error);
    }

    const handleRemoveClicked = () => {
        setSelectedImage(null)
        onRemoveClicked()
        setOcrState(STATUSES.IDLE)
    }

    return (
        <div>
            {selectedImage && (
                <div>
                    <img src={URL.createObjectURL(selectedImage)} alt="scanned file" />
                </div>
            )}
            <div>
                {selectedImage ?
                    <div className="button-container">
                        <button onClick={readImageText}>OCR</button>
                        <button
                            className="remove-button"
                            disabled={ocrState === STATUSES.PENDING}
                            onClick={handleRemoveClicked}
                        >
                            
                        </button>
                    </div>
                    :
                    <>
                        <p></p>
                        <input
                            type="file"
                            name="ocr-image"
                            onChange={(event) => {
                                setSelectedImage(event.target.files[0])
                            }}
                        />
                        <p>:bmpjpgpngpbm</p>
                    </>
                }
            </div>
            <div className="status">
                {ocrState}
            </div>
            <div className="ocr_text">
                {ocrText}
            </div>
            <br />
        </div>
    )
}

export default OcrReader
