import { Radar } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    RadialLinearScale,
    PointElement,
    LineElement,
    Filler,
    Tooltip,
    Legend,
} from 'chart.js';

ChartJS.register(
    RadialLinearScale,
    PointElement,
    LineElement,
    Filler,
    Tooltip,
    Legend
);

export const RadarChart = () => {
    // 
    const data = {
        labels: ['', '', '', '', ''],
        datasets: [
            {
                label: '',
                data: [4, 3, 5, 2, 4], // 15
                backgroundColor: 'rgba(75, 192, 192, 0.2)', // 
                borderColor: 'rgba(75, 192, 192, 1)', // 
                borderWidth: 2, // 
            },
        ],
    };

    // 
    const options = {
        scale: {
            angleLines: {
                display: true, // 
            },
            ticks: {
                beginAtZero: true, // 0
                min: 0, // 
                max: 5, // 
                stepSize: 1, // 
                fontFamily: 'MS Gothic', // 
            },
        },
        legend: {
            labels: {
                fontFamily: 'Arial', // 
            },
        },
    };

    return (
        <div>
            <h2></h2>
            <Radar data={data} />
        </div>
    );
};
